// import Calender from '../calendar/calendar.nvue'
import uniCalendar from '@/components/uni-calendar/uni-calendar.vue'
export default {
	components:{
		// Calender
		uniCalendar
	},
	data(){
		return{
			// 打卡记录
			recodetrain:[],
			// 注意事项
			attentionarray:[],
			// 日历数据
			showCalendar: false,
			info: {
				date: '2019-08-15',
				startDate: '2019-06-15',
				endDate: '2019-010-15',
				// lunar: true,//默认不显示农历
				range: true,
				insert: false,
				selected: []
			}
		}
	},
	onShow(){
		this._initclockin()
	},
	onReady() {
		this.$nextTick(() => {
			this.showCalendar = true
		})
		this.$common.requests('calendarRecord').then(res=>{
			var that = this
			if(res.data.code == 200 ){
				res.data.result.map(item=>{
					that.info.selected.push({date:item})
				})
			}
		})
		// TODO 模拟请求异步同步数据
		setTimeout(() => {
			// this.info.selected = [
			// 	{
			// 		date: '2020-05-07',
			// 	},
			// 	{
			// 		date: '2020-05-20',
			// 	}
			// ]
		}, 500)
	},
	methods:{
		_initclockin(){
			this.$common.requests('showDaKaInfo').then(res=>{
				// 如果上传成功,就重新请求数据
				if(res.data.code == 200){
					this.recodetrain = res.data.result.train
					this.attentionarray = res.data.result.zhuyi
				}
				
			})
		},
		goDetail(id){
			uni.navigateTo({
				url:'clockinDetails?id='+id
			})
		},
		// 今日打卡跳转
		todayclock(){
			uni.navigateTo({
				url:'../trainingCamp/trainingCamp'
			})
		},
		// 日历方法
		change(e) {
			console.log('change 返回:', e)
			// 模拟动态打卡
			// if (this.info.selected.length > 5) return
			// this.info.selected.push({
			// 	date: e.fulldate,
			// 	info: '签到'
			// })
		},
		monthSwitch(e) {
			console.log('monthSwitchs 返回:', e)
		}
	}
}
